import React, {useState} from 'react';
import axios from "axios";
import {Button, createTheme, Tabs, Tab, ThemeProvider} from "@mui/material";
import TextField from '@mui/material/TextField';
import style from './Search.module.css';
import CustomPagination from "../../components/Pagination/CustomPagination";
import SingleContent from "../../components/SingleContent/SingleContent";
import SearchIcon from '@mui/icons-material/Search';

const Search = () => {
  const [type, setType] = useState(0);
  const [searchText, setSearchText] = useState("");
  const [page, setPage] = useState(1);
  const [content, setContent] = useState([]);
  const [numOfPages, setNumOfPages] = useState();

  const theme = createTheme({
     palette: {
       primary: {
         main: '#ffffff',
       },
     },
   });

  const fetchSearch = async () => {
    try {
      const response = await axios.get(`https://api.themoviedb.org/3/search/${type ? "tv" : "movie"}?api_key=${process.env.REACT_APP_API_KEY}&language=en-US&query=${searchText}&page=${page}&include_adult=false`);
      setContent(response.data.results);
      setNumOfPages(response.data.total_pages);
    } catch (error) {
      console.error(error);
    }
  };

  return (
    <>
    <div>
      <ThemeProvider theme={theme}>
        <div className={style.search}>
          <TextField
            style={{flex: 1}}
            className="searchBox"
            label="Search"
            variant="filled"
            onChange={(e) => setSearchText(e.target.value)}
          />
          <Button
            onClick={fetchSearch}
            variant="contained"
            style={{marginLeft: 10}}
          >
            <SearchIcon fontSize="large"/>
          </Button>
        </div>
        <Tabs
          value={type}
          indicatorColor="primary"
          textColor="#fffff"
          onChange={(event, newValue) => {
            setType(newValue);
            setPage(1);
          }}
          style={{paddingBottom: 5}}
          aria-label="disabled tabs example"
        >
          <Tab style={{width: "50%"}} label="Search Movies"/>
          <Tab style={{width: "50%"}} label="Search TV Series"/>
        </Tabs>
      </ThemeProvider>
      <div className={style.searchInput}>
        {content &&
        content.map((c) => (
          <SingleContent
            key={c.id}
            id={c.id}
            poster={c.poster_path}
            title={c.title || c.name}
            date={c.first_air_date || c.release_date}
            media_type={type ? "tv" : "movie"}
            vote_average={c.vote_average}
          />
        ))}
        {searchText &&
        content &&
        (type ? <h2>No Series Found</h2> : <h2>No Movies Found</h2>)}
      </div>
      {numOfPages > 1 && (
        <CustomPagination setPage={setPage} numOfPages={numOfPages}/>
      )}
    </div>
    </>
  );
};

export default Search;










